<template>
    <div class="chooseAddress">
        <header>
            <a href="javascript:history.back(-1)" class="h_a_1">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>选择地址</span>
        </header>
        <section>
            <ul>
                <li v-for="(a,index) in addresses" @click="toOrder(a,index)">
                    <i class="el-icon-success"  :class="{iColor:i == index}"></i>
                    <div>
                        <div class="s1">
                            <span>{{a.name}}</span><span>{{a.sex == 1?"先生":"女士"}}</span><span>{{a.phone}}</span>
                        </div>
                        <div class="s2">
                            <span>{{getTags(a.tag_type)}}</span>
                            <p>{{a.address}}</p>
                        </div>
                    </div>
                </li>
            </ul>
        </section>
        <router-link to="/addAddress" class="a">
            <i class="el-icon-circle-plus-outline"></i>
            <span>新增收货地址</span>
        </router-link>
    </div>
</template>

<script>
    export default {
        name: "ChooseAddress",
        data(){
            return{
                isShow1:true,
                user_id:"",
                addresses:[],
                i:0,
                tags:"",
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            this.axios.get("/v1/users/"+this.user_id+"/addresses").then(res=>{
                this.addresses = res;
                console.log(res);
            }).catch(err=>{})
        },
        methods:{
            toOrder(a,index){
                this.i = index;
                localStorage.setItem("chooseAddress",JSON.stringify(a));
                var time = setInterval(()=>{
                    this.$router.go(-1);
                    clearInterval(time);
                },100)
            },
            getTags(a){
                switch (a) {
                    case 2 :{
                        return this.tags = "家";
                        break;
                    }
                    case 3 :{
                        return this.tags = "学校";
                        break;
                    }
                    case 4 :{
                        return this.tags = "公司";
                        break;
                    }
                    default :{
                        return this.tags = "null";
                    }
                }
            }
        },
    }
</script>

<style scoped>
    .chooseAddress{
        width: 100%;
        background-color: #fff;
        padding-top: 1.95rem;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    section{
        padding-bottom: 5rem;
    }
    section>ul{
        background-color: #fff;
    }
    section>ul>li{
        display: flex;
        align-items: center;
        border-bottom: .025rem solid #f5f5f5;
        padding: .7rem;
        line-height: 1rem;
    }
    section>ul>li>i{
        font-size: 1rem;
        color: #4cd964;
        margin-right: .4rem;
        opacity: 0;
    }
    .iColor{
        opacity: 1;
    }
    .s1{
        font-size: .7rem;
        color: #333;
    }
    .s1>span{
        margin-right: 0.2rem;
    }
    .s1>span:nth-child(1){
        font-size: .8rem;
        font-weight: 700;
    }
    .s2{
        width: 100%;
        display: flex;
        align-items: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .s2>span{
        font-size: .5rem;
        color: #fff;
        border-radius: .15rem;
        background-color: #ff5722;
        height: .6rem;
        line-height: .6rem;
        padding: 0 .2rem;
        margin-right: .3rem;
    }
    .s2>p{
        font-size: .6rem;
        color: #777;
    }
    .a{
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 2.5rem;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 13;
    }
    .a>i{
        font-size: 1.2rem;
        color: #3190e8;
    }
    .a>span{
        font-size: .7rem;
        color: #3190e8;
        margin-left: .3rem;
    }
</style>